.NavSuper {
  display: flex;
  justify-content: center;
  background: #000;
  border-bottom: rgb(90, 77, 0) solid 1px;
}

.NavContainer {
  height: 90px;
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 3rem;
  padding-right: 3rem;
  align-items: center;
  font-size: 0.8rem;
  position: sticky;
  top: 0;
  z-index: 10;
  color: rgb(255, 255, 255);

  /* border: white solid 1px; */
}

.NavLogos {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 250px;

  /* border: white solid 1px; */
}

.NavRightPanel {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 65%;

  /* border: white solid 1px; */
}

.Navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding-right: 30px;
  width: 65%;
  color: gray;

  /* border: rgb(150, 28, 28) solid 2px; */
}

.Mint {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 10%;
  padding-left: 20px;
  padding-right: 20px;
  border-left: aqua solid 4px;
}

.NavSocials {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 10%;
  padding-left: 20px;
  border-left: aqua solid 4px;

  /* border: rgb(49, 206, 67) solid 2px; */
}
