.Nava {
  text-decoration: none;
}

.NavbtnCTA {
  font-family: "Tinos", serif;
  height: 35px;
  width: 90px;
  box-shadow: inset 0px -3px 7px 0px #29bbff;
  background: linear-gradient(to bottom, #0089d9 5%, #00f7ff 100%);
  background-color: #0089d9;
  border-radius: 3px;
  border: 1px solid #0b0e07;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #000000;
  font-size: 24px;
  /* padding: 9px 23px; */
  text-decoration: none;
  text-shadow: 0px 1px 0px #263666;
}
.NavbtnCTA:hover {
  background: linear-gradient(to bottom, #00f7ff 5%, #0089d9 100%);
  background-color: #00f7ff;
}
.NavbtnCTA:active {
  position: relative;
  top: 1px;
}

.NavbtnAPP {
  font-family: "Tinos", serif;
  height: 32px;
  width: 90px;
  box-shadow: inset 0px -3px 7px 0px #1c1c1c;
  background: linear-gradient(to bottom, #a1a1a1 5%, #dbdbdb 100%);
  background-color: #a1a1a1;
  border-radius: 3px;
  border: 1px solid #0b0e07;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #000000;
  font-size: 24px;
  /* padding: 9px 23px; */
  text-decoration: none;
  text-shadow: 0px 1px 0px #263666;
}
.NavbtnAPP:hover {
  background: linear-gradient(to bottom, #dbdbdb 5%, #a1a1a1 100%);
  background-color: #dbdbdb;
}
.NavbtnAPP:active {
  position: relative;
  top: 1px;
}
