.HeroSuper {
  display: flex;
  justify-content: center;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  /* background: #000; */
  color: white;
}

.HeroTable {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 70%;
  height: 700px;

  /* border: white solid 2px; */
}

.HeroLeftPanel {
  display: flex;
  flex-direction: column;
  width: 40%;
  height: 100%;
  justify-content: center;
  align-items: center;

  /* border: rgb(184, 16, 16) solid 2px; */
}

.HeroCard {
  width: 95%;
  height: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.HeroCard1 {
  /* border: rgb(232, 36, 216) solid 2px; */
}

.HeroCard2 {
  /* border: rgb(23, 107, 216) solid 2px; */
}

.HeroCard3 {
  /* border: rgb(208, 238, 40) solid 2px; */
}

.CardBtn {
  padding: 0;
  margin: 0;
  width: 350px;
  height: 175px;
  transition: 0.6s ease-in-out;

  border-radius: 0.8rem;
  cursor: pointer;
}

.CB1 {
  background-image: url("../../images/cassic.png");
  background-size: cover;
  border: 4px solid white;
}

.CB2 {
  background-image: url("../../images/premium.png");
  background-size: cover;
  border: 3px solid gold;
}

.CB3 {
  background-image: url("../../images/deluxe.png");
  background-size: cover;
  border: 3px solid aqua;
}

.CardBtn:hover {
  transform: scale(1.1);
}

.HeroRightPanel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 58%;
  height: 100%;

  /* border: rgb(23, 216, 81) solid 2px; */
}

.CSuper {
  width: 85%;
  height: 75%;
  /* padding-top: 50px; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: justify;
  padding: 30px;

  border-radius: 3rem;
}

.Cp {
  font-family: "Share Tech", sans-serif;
  font-size: 24px;
  color: rgb(194, 194, 194);
}

.Pspan {
  color: gold;
}
.Dspan {
  color: aqua;
}

.CSC {
  border: rgb(125, 125, 125) solid 0px;
  box-shadow: 0 0 7px white, 0 0 10px rgb(222, 222, 222),
    0 0 21px rgb(249, 249, 249), 0 0 42px rgb(231, 231, 231);
  color: rgb(255, 255, 255);
}
.CSP {
  border: rgb(222, 246, 39) solid 5px;
  box-shadow: 0 0 7px white, 0 0 10px rgb(222, 246, 39),
    0 0 21px rgb(222, 246, 39), 0 0 42px rgb(222, 246, 39);
  color: gold;
}
.CSD {
  border: rgb(43, 220, 255) solid 5px;
  box-shadow: 0 0 7px white, 0 0 10px rgb(43, 220, 255),
    0 0 21px rgb(43, 220, 255), 0 0 42px rgb(43, 220, 255);
  color: aqua;
}
